import { AfterViewInit, Component, OnDestroy, OnInit, ViewEncapsulation } from "@angular/core";
import { Router } from "@angular/router";
import { Subscription } from "rxjs";
import { LaborCategoryGroup } from '../../models/lcatgroup.model';
import { LaborCategoryService } from '../../services/laborcat.service';
import { LaborCategoryGroupService } from '../../services/lcatgroup.service';
import { ICellRendererAngularComp } from "ag-grid-angular";
import { GridOptions } from "ag-grid-community";
import { isNil } from "ramda";

declare var jQuery: any;


@Component({
  selector: 'edit-cell',
  template: `<i class="glyphicon glyphicon-pencil" (click)="invokeParentMethod()" data-toggle="modal"  title="Edit LCAT Group"
                   data-keyboard="false" data-backdrop="static" data-target="#edit-lcatgroup"></i>`
  // styles: [
  //     `.btn {
  //         line-height: 0.5
  //     }`
  // ]
})
export class EditLCATGroupComponent implements ICellRendererAngularComp {
  public params: any;

  agInit(params: any): void {
      this.params = params;
  }

  public invokeParentMethod() {
      this.params.context.componentParent.selectEditLCATGroup(this.params.data);
  }

  refresh(): boolean {
      return false;
  }
}

@Component({
  selector: 'delete-cell',
  template: `<i class="glyphicon glyphicon-trash txt-gap" (click)="invokeParentMethod()" data-toggle="modal" title="Delete LCAT Group"
  data-keyboard="false" data-backdrop="static" data-target="#confirm-popup"></i>`
  // styles: [
  //     `.btn {
  //         line-height: 0.5
  //     }`
  // ]
})
export class DeleteLCATGroupComponent implements ICellRendererAngularComp {
  public params: any;

  agInit(params: any): void {
      this.params = params;
  }

  public invokeParentMethod() {
      this.params.context.componentParent.deleteLaborCategory(this.params.data);
  }

  refresh(): boolean {
      return false;
  }
}


// con
@Component({
  selector: "lcatgroup-dashboard",
  templateUrl: "./lcatgroup-dashboard.template.html",
  styleUrls: ["./lcatgroup-dashboard.styles.scss"],
  providers: [LaborCategoryService, LaborCategoryGroupService],
  encapsulation: ViewEncapsulation.None
})
export class LCATGroupDashboardPage implements OnInit, OnDestroy, AfterViewInit {
  public router: Router;
  public data: any[];
  public busyLoadLCATGroups: Subscription;
  public busyDeleteLCATGroup: Subscription;
  public busyLoadLaborCategories: Subscription;
  public alerts: Array<Object>;
  public dropdownData = [];
  public showErrorFlag: boolean;
  public selLCATGroup: LaborCategoryGroup;
  public showCreateLCATGroup: any;
  public showEditLCATGroup: any;

  public delLCATGroupTitle: string = "Delete LCAT Group";
  public delLCATGroupMessage: string = "Are you sure you want to delete LCAT Group?";
  public confirmClicked: boolean = false;
  public cancelClicked: boolean = false;


  public gridOptions: GridOptions;

  public gridApi;
  public gridColumnApi;

  public defaultColDef = {
    sortable: true,
    filter: true,
    headerClass: 'fw-semi-bold'
  };

  public columnDefs = [
    {
      headerName: "",
      field: "value",
      cellRendererFramework: EditLCATGroupComponent,
      colId: "params",
      width: 50,
      filter: false
    },
    {
      headerName: 'Name',
      field: 'name'
    },
    {
      headerName: "",
      field: "value",
      cellRendererFramework: DeleteLCATGroupComponent,
      colId: "params",
      width: 50,
      filter: false
    }
  ];

  constructor(
    private _lcatGroupService: LaborCategoryGroupService,
    router: Router) {
    this.alerts = [
      {
        type: "success",
        msg:
          '<i class="fa fa-circle text-success"></i><span class="alert-text">Successfully generated 12 monthly reports</span>'
      }
    ];

    this.router = router;
    this.showCreateLCATGroup = false;
    this.showEditLCATGroup = false;

    this.gridOptions = <GridOptions>{
      context: {
          componentParent: this
      },
      enableColResize: true,
      rowHeight: 30
    };
  }

  ngOnInit() {
    let searchInput = jQuery("#table-search-input");
    searchInput
      .focus(e => {
        jQuery(e.target)
          .closest(".input-group")
          .addClass("focus");
      })
      .focusout(e => {
        jQuery(e.target)
          .closest(".input-group")
          .removeClass("focus");
      });

      this.loadLCATGroups();
  }

  ngOnDestroy() {
    if (this.busyDeleteLCATGroup) {
      this.busyDeleteLCATGroup.unsubscribe();
    }
    if(this.busyLoadLCATGroups){
      this.busyLoadLCATGroups.unsubscribe();
    }
    if(this.busyLoadLaborCategories){
      this.busyLoadLaborCategories.unsubscribe();
    }
  }

  ngAfterViewInit() {
  }

  onGridReady(params) {
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
  }


  public cosmeticFix(elementID: any){
    // The code below for cosmetic fix - Blue box now surrounds the icon on the search company text box
    let filterInput = jQuery(elementID);
    filterInput
    .focus((e) => {
        jQuery(e.target).closest('.input-group').addClass('focus');
    })
    .focusout((e) => {
        jQuery(e.target).closest('.input-group').removeClass('focus');
    });
  }

  public loadLCATGroups() {
    let that = this;
    this.busyLoadLCATGroups = this._lcatGroupService.getAll().subscribe(lcg => {
      if (lcg) {
        that.data = lcg;
      }
    }, error => {
      this.showErrorFlag = true;
      this.alerts = [];
      this.alerts.push({ type: 'warning', msg: error });
  });
  }

  public deleteLCATGroup(lcg: LaborCategoryGroup) {
    if(!isNil(lcg)){
      this.busyDeleteLCATGroup = this._lcatGroupService.deleteLCATGroupById(lcg.groupId).subscribe(data => {
        if (data) {
          this.loadLCATGroups();
        }
      }, error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: 'warning', msg: error });
       });
    }

  }


  public addLCATGroup() {
    this.showCreateLCATGroup = true;
  }

  public selectEditLCATGroup(lcg) {
    this.showEditLCATGroup = true;
    this.selLCATGroup = lcg;
  }

  public reloadLCATGroups(reload: boolean) {
    this.showEditLCATGroup = false;
    this.showCreateLCATGroup = false;
    if (reload) {
      this.loadLCATGroups();
    } else {
      this.selLCATGroup = new LaborCategoryGroup();
    }
  }

  public onClickBackButton(){
		this.router.navigate(["/app/tdl/search"]);
  }
}
