import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, SimpleChanges, ViewEncapsulation, OnChanges } from "@angular/core";
import { Router } from "@angular/router";
import { isNil } from "ramda";
import { Subscription } from "rxjs";
import { CodeValue } from "../../../tdl/models/codevalue.model";
import { Resource } from "../../../tdl/models/resource.model";
import { ResourceService } from '../../../tdl/services/resource.service';
import { LaborCategory } from '../../models/laborcat.model';
import { AdminService } from "../../services/admin.service";
import { LaborCategoryService } from '../../services/laborcat.service';
/*
@author : Deloitte
this is Componentfor adding contact as a popup.
*/
declare var jQuery: any;

@Component({
	selector: "[edit-resource-popup]",
	templateUrl: "./edit-resource-popup.template.html",
	styleUrls: ["./edit-resource-popup.styles.scss"],
	encapsulation: ViewEncapsulation.None,
	providers: [LaborCategoryService]
})

/**
 *
 *
 * @export
 * @class EditResourcePopup
 * @implements {OnInit}
 */
export class EditResourcePopup implements OnInit, OnDestroy, OnChanges {
	@Input() model;
	@Output() reloadResources = new EventEmitter<boolean>();

	public router: Router;
	public showErrorFlag: boolean;
	public alerts: Array<Object>;
	public editResource = new Resource();

	public laborCategories: LaborCategory[] = [];
	public resourceTypes: CodeValue[] = [];
	public resourceTeams: CodeValue[] = [];
	public positionNames: CodeValue[] = [];
	public listResources: Resource[];

	public busy: Subscription;
	public busylcat: Subscription;
	public busyrtype: Subscription;
	public busyrteam: Subscription;
	public busyLoadPositions: Subscription;
	public busyLoadResources: Subscription;

	public userenabled: boolean;
	public placeholder: boolean;
	public cdm: boolean;


	ngOnInit(): void {
		this.showErrorFlag = false;
		this.loadResources();
	}

	ngOnDestroy() {
		if (this.busy) {
			this.busy.unsubscribe();
		}
		if (this.busylcat) {
			this.busylcat.unsubscribe();
		}
		if (this.busyrtype) {
			this.busyrtype.unsubscribe();
		}
		if (this.busyrteam) {
			this.busyrteam.unsubscribe();
		}
		if (this.busyLoadResources) {
			this.busyLoadResources.unsubscribe();
		}
		if(this.busyLoadPositions){
			this.busyLoadPositions.unsubscribe();
		}
	}

	constructor(
		router: Router,
		private _adminService: AdminService,
		private resourceService: ResourceService,
		private laborCatService: LaborCategoryService
	) {
		this.router = router;

		this.alerts = [
			{
				type: "warning",
				msg:
					'<span class="fw-semi-bold">Warning:</span> Placeholder for Error Validation messages'
			}
		];

		this.showErrorFlag = false;

		this.showLaborCategories();
		this.showResourceTypes();
		this.showResourceTeams();
		this.showPositionNames();
	}

	public loadResources() {
		this.busyLoadResources = this.resourceService
			.getAllActiveResources()
			.subscribe(
				data => {
					if (data) {
						this.listResources = data;
					}
				},
				error => {
					this.showErrorFlag = true;
					this.alerts = [];
					this.alerts.push({ type: "warning", msg: error });
				}
			);
	}

	// Update resource
	public updateResource(resource: Resource) {
		jQuery("#editresourceform")
			.parsley()
			.validate();

		// Toggle to edit mode or navigate to the next screen if validation passes
		if (
			jQuery("#editresourceform")
				.parsley()
				.isValid()
		) {
			resource.displayName = resource.name;
			resource.lookupName = resource.name;
			resource.active = this.userenabled === true ? "Y" : "N";
			resource.placeholder = this.placeholder === true ? "Y" : "N";
			resource.cdm = this.cdm === true ? "Y" : "N";

			this.busy = this._adminService.updateResource(resource).subscribe(
				data => {
					if (data) {
						this.reset(true);
					}
				},
				error => {
					if (!isNil(error) && error.indexOf("USER_EMAIL_UK") > -1) {
						error = "Resource already exists";
					}
					this.showErrorFlag = true;
					this.alerts = [];
					this.alerts.push({
						type: "warning",
						msg: isNil(error) ? "Error: unable to save resource" : error
					});
				}
			);
		}
	}

	public reset(flag) {
		jQuery("#edit-resource")
			.on("hidden")
			.find("#first-name")
			.val("");
		jQuery("#edit-resource")
			.on("hidden")
			.find("#last-name")
			.val("");
		jQuery("#edit-resource")
			.on("hidden")
			.find("#email-address")
			.val("");
		jQuery("#edit-resource")
			.on("hidden")
			.find("#password")
			.val("");
		jQuery("#edit-resource")
			.on("hidden")
			.find("#phone-number")
			.val("");
		jQuery("#edit-resource")
			.on("hidden")
			.find("#res-type")
			.val("");
		jQuery("#edit-resource")
			.on("hidden")
			.find("#res-team")
			.val("");
		jQuery("#edit-resource")
			.on("hidden")
			.find('input[type="email"]')
			.val("");
		jQuery("#edit-resource")
			.on("hidden")
			.find("#maxhours")
			.val("");
		jQuery("#edit-resource")
			.on("hidden")
			.find('input[type="checkbox"]')
			.prop("checked", false);
		jQuery("#editresourceform")
			.parsley()
			.reset();
		jQuery("#edit-resource").modal("hide");
		this.reloadResources.emit(flag);
		this.showErrorFlag = false;
	}

	public showLaborCategories() {
		this.busylcat = this.laborCatService.getAllActive().subscribe(
			data => {
				this.laborCategories = data;
			},
			error => {
				this.showErrorFlag = true;
				this.alerts = [];
				this.alerts.push({ type: "warning", msg: error });
			}
		);
	}

	public showResourceTypes() {
		this.busyrtype = this._adminService.getResourceTypes().subscribe(
			data => {
				this.resourceTypes = data;
			},
			error => {
				this.showErrorFlag = true;
				this.alerts = [];
				this.alerts.push({ type: "warning", msg: error });
			}
		);
	}

	public showResourceTeams() {
		this.busyrteam = this._adminService.getResourceTeams().subscribe(
			data => {
				this.resourceTeams = data;
			},
			error => {
				this.showErrorFlag = true;
				this.alerts = [];
				this.alerts.push({ type: "warning", msg: error });
			}
		);
	}

	public showPositionNames() {
		this.busyLoadPositions = this._adminService.getPositionNames().subscribe(
			data => {
				this.positionNames = data;
			},
			error => {
				this.showErrorFlag = true;
				this.alerts = [];
				this.alerts.push({ type: "warning", msg: error });
			}
		);
	}

	ngOnChanges(changes: SimpleChanges) {
		for (let propName in changes) {
			if (propName === "model") {
				let chg = changes[propName];
				if (chg.currentValue) {
					this.editResource = Object.assign({}, chg.currentValue);
					this.userenabled =
						this.editResource.active === "Y" ? true : false;
					this.placeholder =
						this.editResource.placeholder === "Y" ? true : false;
					this.cdm =
						this.editResource.cdm === "Y" ? true : false;
				}
			}
		}
	}
}
