import { Component, OnDestroy, OnInit, ViewEncapsulation, AfterViewInit } from "@angular/core";
import { Router } from "@angular/router";
import * as _ from "lodash";
import { saveAs } from 'file-saver';
import { Subscription } from "rxjs";
import * as XLSX from 'xlsx';
import { AdminService } from '../../services/admin.service';
import { PipelineProject } from '../../models/pipeline.model';

declare var jQuery: any;

// con
@Component({
  selector: "importpipe-dashboard",
  templateUrl: "./importpipe-dashboard.template.html",
  styleUrls: ["./importpipe-dashboard.styles.scss"],
  providers: [AdminService],
  encapsulation: ViewEncapsulation.None
})
export class ImportPipeDashboardPage implements OnInit, OnDestroy, AfterViewInit {
  public router: Router;
  public data: any[];
  public lstProjects: PipelineProject[]
  public adminCount: number;
  public selLCATGroupId: number;

  public toggleProjectImportStatusTitle: string = "Imported Pipeline Project Status";
  public toggleProjectImportStatusMessage: string = "Are you sure you want to toggle Imported Pipeline Project Status?";
  public confirmClicked: boolean = false;
  public cancelClicked: boolean = false;

  public busyLoadPipelineProjects: Subscription;
  public busyImportingProjectsFromPipeline: Subscription;
  public busyTogglePipelineProjectStatus: Subscription;

  public showErrorFlag: boolean;
  public alerts: Array<Object>;
  public columns:Array<any> = [
    {name: 'flag', type: 'none'}
  ];

  constructor(
        private adminService: AdminService,
        router: Router) {
    this.alerts = [
      {
        type: "success",
        msg:
          '<i class="fa fa-circle text-success"></i><span class="alert-text">Successfully generated 12 monthly reports</span>'
      }
    ];

    this.router = router;
  }

  ngOnInit() {
    let searchInput = jQuery("#table-search-input, #search-countries");
    searchInput
      .focus(e => {
        jQuery(e.target)
          .closest(".input-group")
          .addClass("focus");
      })
      .focusout(e => {
        jQuery(e.target)
          .closest(".input-group")
          .removeClass("focus");
      });

      this.loadImportedPipelineProjects();
  }

  ngOnDestroy() {
    if (this.busyLoadPipelineProjects) {
      this.busyLoadPipelineProjects.unsubscribe();
    }
    if(this.busyImportingProjectsFromPipeline){
      this.busyImportingProjectsFromPipeline.unsubscribe();
    }
    if(this.busyTogglePipelineProjectStatus){
      this.busyTogglePipelineProjectStatus.unsubscribe();
    }
  }

  ngAfterViewInit() {
    for(let col of this.columns){
        if(col.filtering){
            this.cosmeticFix('#filter-' + col.name);
        }
    }
  }

  public cosmeticFix(elementID: any){
    // The code below for cosmetic fix - Blue box now surrounds the icon on the search company text box
    let filterInput = jQuery(elementID);
    filterInput
    .focus((e) => {
        jQuery(e.target).closest('.input-group').addClass('focus');
    })
    .focusout((e) => {
        jQuery(e.target).closest('.input-group').removeClass('focus');
    });
  }

  private loadImportedPipelineProjects() {
    let that = this;
    this.busyLoadPipelineProjects = this.adminService.getImportedPipelineProjects().subscribe(projdata => {
      if (projdata) {
        that.data = projdata;
      }
    }, error => {
      this.showErrorFlag = true;
      this.alerts = [];
      this.alerts.push({ type: 'warning', msg: error });
    });
  }

  public toggleProjectImportStatus(selproj: PipelineProject){
    let that = this;

    this.busyTogglePipelineProjectStatus = this.adminService.togglePipelineProjectImportStatus(selproj).subscribe(projdata => {
      if (projdata) {
        that.loadImportedPipelineProjects();
      }
    }, error => {
      this.showErrorFlag = true;
      this.alerts = [];
      this.alerts.push({ type: 'warning', msg: error });
    });
  }

  public addimportproject(selproj: PipelineProject){
    let that = this;

    this.busyImportingProjectsFromPipeline = this.adminService.addPipelineProject(selproj).subscribe(projdata => {
      if (projdata) {
        that.loadImportedPipelineProjects();
      }
    }, error => {
      this.showErrorFlag = true;
      this.alerts = [];
      this.alerts.push({ type: 'warning', msg: error });
    });
  }


  public importPipelineProjects(){
    let that = this;

    this.busyImportingProjectsFromPipeline = this.adminService.addRemainingPipelineProjects().subscribe(projdata => {
      if (projdata) {
        that.loadImportedPipelineProjects();
      }
    }, error => {
      this.showErrorFlag = true;
      this.alerts = [];
      this.alerts.push({ type: 'warning', msg: error });
    });
  }

  private s2ab(s) {
    let buf = new ArrayBuffer(s.length);
    let view = new Uint8Array(buf);
    for (var i=0; i !== s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
    return buf;
  }

  public exportLaborCategoriesReport(data:any, filename, sheetName){
    let ws:XLSX.WorkSheet = XLSX.utils.json_to_sheet(data);
    let wb:XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, sheetName);

    /* save to file */
    let wbout: string = XLSX.write(wb, { bookType: 'xlsx', type: 'binary' });
    saveAs(new Blob([this.s2ab(wbout)]), filename);
  }

  public downloadLaborCategories(){
    this.exportLaborCategoriesReport(this.data, 'laborcat-report.xlsx', 'Labor Categories');
  }

  public onChangeTable(filterinfo):void {
      // if(filterinfo){
      //     if(filterinfo.columnName === "code"){
      //         this.laborcatfilter.codeFilter = filterinfo.filterString;
      //         this.laborcatfilter.codeFilter = this.laborcatfilter.codeFilter.trim();
      //     }
      //     if(filterinfo.refresh){
      //         this.refreshLaborCatFilterObject();
      //     }
      // }
  }

  public FilterSelectionChanged(event, filterColumnName){
  }

  public ApplyFilter(){
      // this.refreshLaborCatFilterObject();
  }


  public updateFilterTextBox(colName, colText){
      let rowIndex = _.findIndex(this.columns, function (o) { return (o.name && o.name.toString() === colName)});
      if(rowIndex !== -1) {
          let selRow = this.columns[rowIndex];
          if(selRow.filtering){
              selRow.filtering.filterString = colText;
          }
      }
  }
}
