
import * as _ from 'lodash';
import { Pipe, PipeTransform } from '@angular/core';
import { ResourceFilterData } from '../models/res-filter.model';
import { isNil } from 'ramda';

@Pipe({
    name: "resfilterpipe"
})
export class ResourceFilterPipe implements PipeTransform {
    transform(data: any[], filter: ResourceFilterData): any {
        let results = data;

        if (data) {
            if (filter) {
                let handler = this;
                // filter by tdl name
                if (typeof filter.nameFilter !== 'undefined' && filter.nameFilter.length > 0) {
                    results =  _.filter(results, function(item) {
                        let val1:string = item.name;
                        let val2:string = filter.nameFilter;
                        return val1.toUpperCase().indexOf(val2.toUpperCase()) > -1;
                    });
                }
                // filter by placeholder
                if (typeof filter.placeholderFilter !== 'undefined' && filter.placeholderFilter.length > 0) {
                    results =  _.filter(results, function(item) {
                         let val1:string = isNil(item.placeholder) ? "" : item.placeholder; 
                         let val2:string = isNil(filter.placeholderFilter) ? "" : filter.placeholderFilter;
                         return val1.toUpperCase().indexOf(val2.toUpperCase()) > -1;
                     });
                 }                  
            }
        }
        return results;
    }
// tslint:disable-next-line:eofline
}