/*
@author : Deloitte
this is Component for creating a labor category
*/
import { Component, Output, EventEmitter, ViewEncapsulation, OnInit, OnDestroy, Input } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { CodeValue } from '../../../tdl/models/codevalue.model';
import { LaborCategory } from '../../models/laborcat.model';
import { LaborCategoryService } from '../../services/laborcat.service';
declare var jQuery: any;

@Component({
  selector: '[create-laborcat-popup]',
  templateUrl: './create-laborcat-popup.template.html',
  styleUrls: ['./create-laborcat-popup.styles.scss'],
  encapsulation: ViewEncapsulation.None
})

/**
 *
 *
 * @export
 * @class CreateLaborCatPopup
 * @implements {OnInit}
 */

export class CreateLaborCatPopup implements OnInit, OnDestroy {
  @Output() reloadLaborCategories = new EventEmitter<boolean>();
  @Input() LCATGroupId;

  public router: Router;
  public showErrorFlag: boolean;
  public alerts: Array<Object>;
  public laborCat = new LaborCategory();
  public busy: Subscription;
  public busyLoadLaborCategories:Subscription;
  public busyLoadSkills: Subscription;

  public userenabled: boolean;
  public laborCategories: LaborCategory[] = [];
  public listSkills: CodeValue[]=[];

  constructor(router: Router,
    private _laborCatService: LaborCategoryService
  ) {
    this.router = router;

    this.alerts = [
      {
        type: 'warning',
        msg: '<span class="fw-semi-bold">Warning:</span> Placeholder for Error Validation messages'
      }
    ];

    this.showErrorFlag = false;

    this.showLaborCategories();
    this.loadLCATSkills();

  }

  ngOnInit(): void {
    this.showErrorFlag = false;
  }

  ngOnDestroy() {
    if (this.busy) {
      this.busy.unsubscribe();
    }
    if(this.busyLoadSkills){
      this.busyLoadSkills.unsubscribe();
    }
    if(this.busyLoadLaborCategories){
      this.busyLoadLaborCategories.unsubscribe();
    }
  }

  // Create a new Labor Cat
  public createLaborCat(laborCat: LaborCategory) {
    jQuery('#addlaborcatform').parsley().validate();

    // Toggle to edit mode or navigate to the next screen if validation passes
    if (jQuery('#addlaborcatform').parsley().isValid()) {

      laborCat.groupId = this.LCATGroupId;
      this.busy = this._laborCatService.saveLaborCategory(laborCat).subscribe(data => {
        if (data) {
          this.reset(true);
          this.reloadLaborCategories.emit(true);
        }
      }, error => {
          this.showErrorFlag = true;
          this.alerts = [];
          this.alerts.push({ type: 'warning', msg: error });
      });
    }

  }

  public reset(flag) {
    jQuery('#create-laborcat').on('hidden').find('#code').val('');
    jQuery('#create-laborcat').on('hidden').find('#description').val('');
    jQuery("#create-laborcat").on("hidden").find("#base-rate").val("");
    jQuery("#create-laborcat").on("hidden").find("#op1-rate").val("");
    jQuery("#create-laborcat").on("hidden").find("#op2-rate").val("");
    jQuery("#create-laborcat").on("hidden").find("#op3-rate").val("");
    jQuery("#create-laborcat").on("hidden").find("#op4-rate").val("");
    jQuery("#create-laborcat").on("hidden").find("#op5-rate").val("");
    jQuery('#addlaborcatform').parsley().reset();
    jQuery('#create-laborcat').modal('hide');
    this.showErrorFlag = false;
  }

  public loadLCATSkills(){
  		this._laborCatService.getAllLCATSkills().subscribe(data => {
        this.listSkills = data;
      },
      error => {
  				this.showErrorFlag = true;
  				this.alerts = [];
  				this.alerts.push({ type: "warning", msg: error });
  			}
  		);
  	}

  public showLaborCategories() {
		this.busyLoadLaborCategories = this._laborCatService.getAll().subscribe(data => {
      this.laborCategories = data;
    },
    error => {
				this.showErrorFlag = true;
				this.alerts = [];
				this.alerts.push({ type: "warning", msg: error });
			}
		);
	}

}
