import { AfterViewInit, Component, OnDestroy, OnInit, ViewEncapsulation } from "@angular/core";
import { Router } from "@angular/router";
import * as _ from "lodash";
import { Subscription } from "rxjs";
import { LaborCategoryGroup } from '../../models/lcatgroup.model';
import { LaborCategoryService } from '../../services/laborcat.service';
import { LaborCategoryGroupService } from '../../services/lcatgroup.service';
import { LaborCategory } from '../../models/laborcat.model';
import { ICellRendererAngularComp } from "ag-grid-angular";
import { GridOptions } from "ag-grid-community";
import { isNil } from "ramda";

declare var jQuery: any;


@Component({
  selector: 'edit-cell',
  template: `<i class="glyphicon glyphicon-pencil" (click)="invokeParentMethod()" data-toggle="modal" title="Edit Labor Category"
                   data-keyboard="false" data-backdrop="static" data-target="#edit-laborcat"></i>`
  // styles: [
  //     `.btn {
  //         line-height: 0.5
  //     }`
  // ]
})
export class EditLCATComponent implements ICellRendererAngularComp {
  public params: any;

  agInit(params: any): void {
      this.params = params;
  }

  public invokeParentMethod() {
      this.params.context.componentParent.selectEditLaborCategory(this.params.data);
  }

  refresh(): boolean {
      return false;
  }
}

@Component({
  selector: 'delete-cell',
  template: `<i class="glyphicon glyphicon-trash txt-gap" (click)="invokeParentMethod()" data-toggle="modal"  title="Delete Labor Category"
  data-keyboard="false" data-backdrop="static" data-target="#confirm-popup"></i>`
  // styles: [
  //     `.btn {
  //         line-height: 0.5
  //     }`
  // ]
})
export class DeleteLCATComponent implements ICellRendererAngularComp {
  public params: any;

  agInit(params: any): void {
      this.params = params;
  }

  public invokeParentMethod() {
      this.params.context.componentParent.deleteLaborCategory(this.params.data);
  }

  refresh(): boolean {
      return false;
  }
}

// con
@Component({
  selector: "laborcat-dashboard",
  templateUrl: 'laborcat-dashboard.template.html',
  styleUrls: ["./laborcat-dashboard.styles.scss"],
  providers: [LaborCategoryService, LaborCategoryGroupService],
  encapsulation: ViewEncapsulation.None
})
export class LaborCatDashboardPage implements OnInit, OnDestroy, AfterViewInit {
  public router: Router;
  public data: any[];
  public lcatGroups: LaborCategoryGroup[]
  public adminCount: number;
  public selLCATGroupId: number;

  public busyLoadLaborCategories: Subscription;
  public busyLoadLCATGroups: Subscription;
  public busyDeleteLaborCategory: Subscription;

  public dropdownData = [];
  public showErrorFlag: boolean;
  public alerts: Array<Object>;
  public effectiveDate: Date;
  public selLaborCategory: LaborCategory;
  public showCreateLaborCategory: any;
  public showEditLaborCategory: any;

  public delLaborCategoryTitle: string = "Delete Labor Category";
  public delLaborCategoryMessage: string = "Are you sure you want to delete Labor Category?";
  public confirmClicked: boolean = false;
  public cancelClicked: boolean = false;

  public gridOptions: GridOptions;

  public gridApi;
  public gridColumnApi;

  public defaultColDef = {
    sortable: true,
    filter: true,
    headerClass: 'fw-semi-bold'
  };

  public columnDefs = [
    {
      headerName: "",
      field: "value",
      cellRendererFramework: EditLCATComponent,
      colId: "params",
      width: 50,
      filter: false
    },
    {
      headerName: 'Code',
      field: 'code',
      cellClass: function(params) {
        return params.data.enabled === false ? "fw-semi-bold inactive" : "fw-semi-bold ";
      }
    },
    {
      headerName: 'Description',
      field: 'description',
      cellClass: function(params) {
        return params.data.enabled === false ? "inactive" : "";
      }
    },
    {
      headerName: 'Skill',
      field: 'skillName',
      cellClass: function(params) {
        return params.data.enabled === false ? "inactive" : "";
      }
    },
    {
      headerName: 'Base Rate',
      field: 'baseRate',
      cellClass: function(params) {
        return params.data.enabled === false ? "inactive" : "";
      }
    },
    {
      headerName: 'Active',
      field: 'enabled',
      cellClass: function(params) {
        return params.data.enabled === false ? "inactive" : "";
      }
    },
    {
      headerName: "",
      field: "value",
      cellRendererFramework: DeleteLCATComponent,
      colId: "params",
      width: 50,
      filter: false
    }
  ];

  constructor(
    private _laborCatService: LaborCategoryService,
    private _lcatGroupService: LaborCategoryGroupService,
    router: Router) {
    this.alerts = [
      {
        type: "success",
        msg:
          '<i class="fa fa-circle text-success"></i><span class="alert-text">Successfully generated 12 monthly reports</span>'
      }
    ];

    this.router = router;
    this.showCreateLaborCategory = false;
    this.showEditLaborCategory = false;
    this.selLCATGroupId = 1;

    this.gridOptions = <GridOptions>{
      context: {
          componentParent: this
      },
      enableColResize: true,
      rowHeight: 30
    };
  }

  ngOnInit() {
    let searchInput = jQuery("#table-search-input, #search-countries");
    searchInput
      .focus(e => {
        jQuery(e.target)
          .closest(".input-group")
          .addClass("focus");
      })
      .focusout(e => {
        jQuery(e.target)
          .closest(".input-group")
          .removeClass("focus");
      });

      this.loadLCATGroups();
  }

  ngOnDestroy() {
    if (this.busyLoadLaborCategories) {
      this.busyLoadLaborCategories.unsubscribe();
    }
    if (this.busyDeleteLaborCategory) {
      this.busyDeleteLaborCategory.unsubscribe();
    }
    if(this.busyLoadLCATGroups){
      this.busyLoadLCATGroups.unsubscribe();
    }
  }

  ngAfterViewInit() {
  }


  onGridReady(params) {
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
  }


  public cosmeticFix(elementID: any){
    // The code below for cosmetic fix - Blue box now surrounds the icon on the search company text box
    let filterInput = jQuery(elementID);
    filterInput
    .focus((e) => {
        jQuery(e.target).closest('.input-group').addClass('focus');
    })
    .focusout((e) => {
        jQuery(e.target).closest('.input-group').removeClass('focus');
    });
  }

  private loadLCATGroups() {
    let that = this;
    this.busyLoadLCATGroups = this._lcatGroupService.getAll().subscribe(lcg => {
      if (lcg) {
        that.lcatGroups = lcg;
        that.loadLaborCategories(this.selLCATGroupId);
      }
    }, error => {
      this.showErrorFlag = true;
      this.alerts = [];
      this.alerts.push({ type: 'warning', msg: error });
  });
  }

  private loadLaborCategories(groupId: number) {

    this.busyLoadLaborCategories = this._laborCatService.getLaborCategoryByGroup(groupId).subscribe(laborcats => {
      if (laborcats) {
        this.data = laborcats;
      }
    }, error => {
      this.showErrorFlag = true;
      this.alerts = [];
      this.alerts.push({ type: 'warning', msg: error });
  });
  }

  public reloadLaborCategories(reload: boolean) {
    this.showCreateLaborCategory = false;
    this.showEditLaborCategory = false;
    if (reload) {
      this.loadLaborCategories(this.selLCATGroupId);
    } else {
      this.selLaborCategory = new LaborCategory();
    }
  }

  public deleteLaborCategory(lcat: LaborCategory) {
    if(!isNil(lcat)){
      this.busyDeleteLaborCategory = this._laborCatService.deleteLaborCategoryById(lcat.id).subscribe(data => {
      if (data) {
          this.reloadLaborCategories(true);
        }
      }, error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: 'warning', msg: error });
      });
    }
  }

  public addLaborCategory() {
    this.showCreateLaborCategory = true;
  }

  public selectEditLaborCategory(laborcat) {
    this.showEditLaborCategory = true;
    this.selLaborCategory = laborcat;
  }

  public onLCATGroupChanged(lcatGroupId: number){
    this.selLCATGroupId = lcatGroupId;
    this.reloadLaborCategories(true);
  }

  public onClickBackButton(){
		this.router.navigate(["/app/tdl/search"]);
  }

}
