import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, SimpleChanges, ViewEncapsulation } from "@angular/core";
import { Router } from "@angular/router";
import { Subscription } from "rxjs";
import { LaborCategoryGroup } from '../../models/lcatgroup.model';
import { LaborCategoryGroupService } from '../../services/lcatgroup.service';
declare var jQuery: any;

@Component({
  selector: "[edit-lcatgroup-popup]",
  templateUrl: "./edit-lcatgroup-popup.template.html",
  styleUrls: ["./edit-lcatgroup-popup.styles.scss"],
  encapsulation: ViewEncapsulation.None
})

/**
 *
 *
 * @export
 * @class EditLaborCatPopup
 * @implements {OnInit}
 */
export class EditLCATGroupPopup implements OnInit, OnDestroy {
  @Input() model;
  @Output() reloadLCATGroups = new EventEmitter<boolean>();

  public router: Router;
  public showErrorFlag: boolean;
  public alerts: Array<Object>;
  public editLCATGroup: LaborCategoryGroup;
  public busy: Subscription;

  ngOnInit(): void {
    this.showErrorFlag = false;
  }

  ngOnDestroy() {
    if (this.busy) {
      this.busy.unsubscribe();
    }
  }

  constructor(
    router: Router,
    private _lcatGroupService: LaborCategoryGroupService
  ) {
    this.router = router;

    this.alerts = [
      {
        type: "warning",
        msg:
          '<span class="fw-semi-bold">Warning:</span> Placeholder for Error Validation messages'
      }
    ];

    this.showErrorFlag = false;
  }

  // Update labor cat
  public updateLCATGroup(lcg: LaborCategoryGroup) {
    jQuery("#editlcatgroupform").parsley().validate();

    // Toggle to edit mode or navigate to the next screen if validation passes
    if ( jQuery("#editlcatgroupform").parsley().isValid() ) {

      this.busy = this._lcatGroupService.saveLaborCategoryGroup(lcg).subscribe(
        data => {
          if (data) {
            this.reset(true);
          }
        },
        error => {
          this.showErrorFlag = true;
          this.alerts = [];
          this.alerts.push({ type: "warning", msg: error });
        }
      );
    }
  }

  public reset(flag) {
    jQuery("#edit-lcatgroup").on("hidden").find("#code").val("");
    jQuery("#edit-lcatgroup").on("hidden").find("#description").val("");
    jQuery("#editlcatgroupform").parsley().reset();
    jQuery("#edit-lcatgroup").modal("hide");
    this.reloadLCATGroups.emit(flag);
    this.showErrorFlag = false;
  }
  ngOnChanges(changes: SimpleChanges) {
    for (let propName in changes) {
      if (propName === "model") {
        let chg = changes[propName];
        if (chg.currentValue) {
          this.editLCATGroup = Object.assign({}, chg.currentValue);
        }
      }
    }
  }
}
