import { AfterViewInit, Component, OnDestroy, OnInit, ViewEncapsulation } from "@angular/core";
import { Router } from "@angular/router";
import { ICellRendererAngularComp } from "ag-grid-angular";
import { GridOptions } from "ag-grid-community";
import "ag-grid-enterprise";
import "ag-grid-enterprise/chartsModule";
import { isNil } from "ramda";
import { Subscription } from "rxjs";
import { Resource } from '../../../tdl/models/resource.model';
import { ResourceService } from '../../../tdl/services/resource.service';

declare var jQuery: any;


@Component({
  selector: 'edit-cell',
  template: `<i class="glyphicon glyphicon-pencil" (click)="invokeParentMethod()" data-toggle="modal" title="Edit Resource"
                   data-keyboard="false" data-backdrop="static" data-target="#edit-resource"></i>`
  // styles: [
  //     `.btn {
  //         line-height: 0.5
  //     }`
  // ]
})
export class EditResourceComponent implements ICellRendererAngularComp {
  public params: any;

  agInit(params: any): void {
      this.params = params;
  }

  public invokeParentMethod() {
      this.params.context.componentParent.selectEditResource(this.params.data);
  }

  refresh(): boolean {
      return false;
  }
}

@Component({
  selector: 'delete-cell',
  template: `<i class="glyphicon glyphicon-trash txt-gap" (click)="invokeParentMethod()" data-toggle="modal" title="Delete Resource"
  data-keyboard="false" data-backdrop="static" data-target="#confirm-popup"></i>`
  // styles: [
  //     `.btn {
  //         line-height: 0.5
  //     }`
  // ]
})
export class DeleteResourceComponent implements ICellRendererAngularComp {
  public params: any;

  agInit(params: any): void {
      this.params = params;
  }

  public invokeParentMethod() {
      this.params.context.componentParent.deleteResource(this.params.data);
  }

  refresh(): boolean {
      return false;
  }
}


// con
@Component({
  selector: "resource-dashboard",
  templateUrl: 'resource-dashboard.template.html',
  styleUrls: ["./resource-dashboard.styles.scss"],
  providers: [ResourceService],
  encapsulation: ViewEncapsulation.None
})
export class ResourceDashboardPage implements OnInit, OnDestroy, AfterViewInit {
  public router: Router;
  public data: any[];
  public adminCount: number;

  public busyLoadResources: Subscription;
  public busyDeleteResource: Subscription;

  public dropdownData = [];
  public showErrorFlag: boolean;
  public alerts: Array<Object>;
  public effectiveDate: Date;
  public selResource: Resource;
  public showCreateResource: any;
  public showEditResource: any;

  public delresourcetitle: string = "Delete Resource";
  public delresourcemessage: string = "Are you sure you want to delete resource?";
  public confirmClicked: boolean = false;
  public cancelClicked: boolean = false;

  public gridOptions: GridOptions;

  public gridApi;
  public gridColumnApi;

  public defaultColDef = {
    sortable: true,
    filter: true,
    headerClass: 'fw-semi-bold'
  };

  public columnDefs = [
    {
      headerName: "",
      field: "value",
      cellRendererFramework: EditResourceComponent,
      colId: "params",
      width: 50,
      filter: false
    },
    {
      headerName: 'Name',
      field: 'name',
      cellClass: function(params) {
        return params.data.active !== 'Y' ? "fw-semi-bold inactive" : "fw-semi-bold ";
      }
    },
    {
      headerName: 'Placeholder',
      field: 'placeholder',
      cellClass: function(params) {
        return params.data.active !== 'Y' ? "inactive" : "";
      }
    },
    {
      headerName: 'Position',
      field: 'positionName',
      cellClass: function(params) {
        return params.data.active !== 'Y' ? "inactive" : "";
      }
    },
    {
      headerName: 'Active',
      field: 'active',
      cellClass: function(params) {
        return params.data.active !== 'Y' ? "inactive" : "";
      }
    },
    {
      headerName: 'CDM',
      field: 'cdm',
      cellClass: function(params) {
        return params.data.active !== 'Y' ? "inactive" : "";
      }
    },
    {
      headerName: "",
      field: "value",
      cellRendererFramework: DeleteResourceComponent,
      colId: "params",
      width: 50,
      filter: false
    }
  ];

  constructor(private resourceService: ResourceService, router: Router) {
    this.alerts = [
      {
        type: "success",
        msg:
          '<i class="fa fa-circle text-success"></i><span class="alert-text">Successfully generated 12 monthly reports</span>'
      }
    ];

    this.router = router;
    this.showCreateResource = false;
    this.showEditResource = false;

    this.gridOptions = <GridOptions>{
      context: {
          componentParent: this
      },
      enableColResize: true,
      rowHeight: 30
    };
  }

  ngOnInit() {
    let searchInput = jQuery("#table-search-input, #search-countries");
    searchInput
      .focus(e => {
        jQuery(e.target)
          .closest(".input-group")
          .addClass("focus");
      })
      .focusout(e => {
        jQuery(e.target)
          .closest(".input-group")
          .removeClass("focus");
      });

    // load the users
    this.loadResources();
  }

  ngOnDestroy() {
    if (this.busyLoadResources) {
      this.busyLoadResources.unsubscribe();
    }
    if (this.busyDeleteResource) {
      this.busyDeleteResource.unsubscribe();
    }
  }

  ngAfterViewInit() {
   }


  onGridReady(params) {
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
  }

  public cosmeticFix(elementID: any){
    // The code below for cosmetic fix - Blue box now surrounds the icon on the search company text box
    let filterInput = jQuery(elementID);
    filterInput
    .focus((e) => {
        jQuery(e.target).closest('.input-group').addClass('focus');
    })
    .focusout((e) => {
        jQuery(e.target).closest('.input-group').removeClass('focus');
    });
  }

  private loadResources() {
    let that = this;
    this.busyLoadResources = this.resourceService.getAllNonPlaceholderResourcesOnly().subscribe(resources => {
      if (resources) {
        that.data = resources;
      }
    }, error => {
      that.showErrorFlag = true;
      that.alerts = [];
      that.alerts.push({ type: 'warning', msg: error });
  });
  }

  public reloadResources(reload: boolean) {
    this.showCreateResource = false;
    this.showEditResource = false;
    if (reload) {
      this.loadResources();
    } else {
      this.selResource = new Resource();
    }
  }

  public deleteResource(res: Resource) {
    if(!isNil(res)){
      this.busyDeleteResource = this.resourceService.deleteResourceById(res.resourceID).subscribe(data => {
        if (data) {
          this.reloadResources(true);
        }
      }, error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: 'warning', msg: error });
      });
    }
  }

  public addResource() {
    this.showCreateResource = true;
  }

  public selectEditResource(resource) {
    this.showEditResource = true;
    this.selResource = resource;
  }

  public refreshResourceFilterObject() {
  }

  public launchSkillsSurvey(resourceId: number){
    this.router.navigate(['app/resource/survey', resourceId]);
  }
  public onClickBackButton(){
		this.router.navigate(["/app/tdl/search"]);
  }
}
