import { Injectable } from '@angular/core';
import { Observable, of } from "rxjs";
import { LaborCategoryGroup } from '../models/lcatgroup.model';
import { tap, catchError } from 'rxjs/operators';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';

const httpOptions = {
	headers: new HttpHeaders({ "Content-Type": "application/json" })
};

/**
 *
 *
 * @export
 * @class LaborCategoryGroupService
 */
@Injectable()
export class LaborCategoryGroupService {

    private getLCATGroupUrl = environment.apiurl + '/api/v1/lcatgroups';

    constructor(private http: HttpClient) { }

    public saveLaborCategoryGroup(lcatGroup: LaborCategoryGroup): Observable<LaborCategoryGroup> {
        return this.http.post(this.getLCATGroupUrl, lcatGroup, httpOptions).pipe(
			tap(_ => this.log("saveLaborCategory")),
			catchError(this.handleError<any>("saveLaborCategory"))
		);
    }


    public getLaborCategoryGroup(id) {
        return this.http.get(this.getLCATGroupUrl + '/' + id, httpOptions).pipe(
			tap(_ => this.log("getLaborCategoryGroup")),
			catchError(this.handleError<any>("getLaborCategoryGroup"))
		);
    }

    public deleteLCATGroupById(id: number){
        return this.http.delete(this.getLCATGroupUrl + '/' + id, httpOptions).pipe(
			tap(_ => this.log("deleteLCATGroupById")),
			catchError(this.handleError<any>("deleteLCATGroupById"))
		);
    }

    /**
     *
     *
     * @param {laborCategoryId} laborCategoryId
     * @returns
     *
     * @memberOf LaborCategoryGroupService
     */
    public getAll(){
        return this.http.get(this.getLCATGroupUrl, httpOptions).pipe(
			tap(_ => this.log("getAll")),
			catchError(this.handleError<any>("getAll"))
		);
    }


        /**
     *
     *
     * @param {laborCategoryId} laborCategoryId
     * @returns
     *
     * @memberOf LaborCategoryGroupService
     */
    public getAllActive(){
        return this.http.get(this.getLCATGroupUrl + "/active", httpOptions).pipe(
			tap(_ => this.log("getAllActive")),
			catchError(this.handleError<any>("getAllActive"))
		);
    }


  	/**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
	private handleError<T>(operation = "operation", result?: T) {
		return (error: any): Observable<T> => {
			// TODO: send the error to remote logging infrastructure
			console.error(error); // log to console instead

			// TODO: better job of transforming error for user consumption
			this.log("${operation} failed: ${error.message}");

			// Let the app keep running by returning an empty result.
			return of(result as T);
		};
	}

	/** Log a Attribute Service message with the MessageService */
	private log(message: string) {
		//this.messageService.add("Profile Service: ${message}");
	}

}