import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, SimpleChanges, ViewEncapsulation } from "@angular/core";
import { Router } from "@angular/router";
import { Subscription } from "rxjs";
import { CodeValue } from '../../../tdl/models/codevalue.model';
import { LaborCategory } from '../../models/laborcat.model';
import { LaborCategoryService } from '../../services/laborcat.service';
declare var jQuery: any;

@Component({
  selector: "[edit-laborcat-popup]",
  templateUrl: "./edit-laborcat-popup.template.html",
  styleUrls: ["./edit-laborcat-popup.styles.scss"],
  encapsulation: ViewEncapsulation.None
})

/**
 *
 *
 * @export
 * @class EditLaborCatPopup
 * @implements {OnInit}
 */
export class EditLaborCatPopup implements OnInit, OnDestroy {
  @Input() model;
  @Output() reloadLaborCategories = new EventEmitter<boolean>();

  public router: Router;
  public showErrorFlag: boolean;
  public alerts: Array<Object>;
  public editLaborCat: LaborCategory;
  public busy: Subscription;
  public busyLoadLaborCategories:Subscription;
  public busyLoadSkills: Subscription;

  public laborCategories: LaborCategory[] = [];
  public listSkills: CodeValue[]=[];

  ngOnInit(): void {
    this.showErrorFlag = false;
  }

  ngOnDestroy() {
    if (this.busy) {
      this.busy.unsubscribe();
    }
    if(this.busyLoadSkills){
      this.busyLoadSkills.unsubscribe();
    }
    if(this.busyLoadLaborCategories){
      this.busyLoadLaborCategories.unsubscribe();
    }
  }

  constructor(
    router: Router,
    private _laborCatService: LaborCategoryService
  ) {
    this.router = router;

    this.alerts = [
      {
        type: "warning",
        msg:
          '<span class="fw-semi-bold">Warning:</span> Placeholder for Error Validation messages'
      }
    ];

    this.showErrorFlag = false;

    this.showLaborCategories();
    this.loadLCATSkills();
  }

  // Update labor cat
  public updateLaborCat(laborCat: LaborCategory) {
    jQuery("#editlaborcatform").parsley().validate();

    // Toggle to edit mode or navigate to the next screen if validation passes
    if ( jQuery("#editlaborcatform").parsley().isValid() ) {
      laborCat.code = laborCat.code.toUpperCase();

      this.busy = this._laborCatService.saveLaborCategory(laborCat).subscribe(
        data => {
          if (data) {
            this.reset(true);
          }
        },
        error => {
          this.showErrorFlag = true;
          this.alerts = [];
          this.alerts.push({ type: "warning", msg: error });
        }
      );
    }
  }

  public reset(flag) {
    jQuery("#edit-laborcat").on("hidden").find("#code").val("");
    jQuery("#edit-laborcat").on("hidden").find("#description").val("");
    jQuery("#edit-laborcat").on("hidden").find("#base-rate").val("");
    jQuery("#edit-laborcat").on("hidden").find("#op1-rate").val("");
    jQuery("#edit-laborcat").on("hidden").find("#op2-rate").val("");
    jQuery("#edit-laborcat").on("hidden").find("#op3-rate").val("");
    jQuery("#edit-laborcat").on("hidden").find("#op4-rate").val("");
    jQuery("#edit-laborcat").on("hidden").find("#op5-rate").val("");
    jQuery("#editlaborcatform").parsley().reset();
    jQuery("#edit-laborcat").modal("hide");
    this.reloadLaborCategories.emit(flag);
    this.showErrorFlag = false;
  }

  public loadLCATSkills(){
    this._laborCatService.getAllLCATSkills().subscribe(data => {
      this.listSkills = data;
    },
    error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: "warning", msg: error });
      }
    );
  }

  public showLaborCategories() {
    this._laborCatService.getAll().subscribe(data => {
      this.laborCategories = data;
    },
    error => {
        this.showErrorFlag = true;
        this.alerts = [];
        this.alerts.push({ type: 'warning', msg: error });
    });
  }

  ngOnChanges(changes: SimpleChanges) {
    for (let propName in changes) {
      if (propName === "model") {
        let chg = changes[propName];
        if (chg.currentValue) {
          this.editLaborCat = Object.assign({}, chg.currentValue);
        }
      }
    }
  }
}
